.App {
  text-align: center;
  box-sizing: border-box;
}



.App-header {
    background-color: #1D428A;
    box-shadow: -1px 0 5px 0 #1D428A;
    margin-bottom: 20px;
    padding: 10px 0 5px 40px;
    height: 60px;
}

.App-logo {
    float: left;
    height: 40px;
}

.App-title {
    color: white;
    float: left;
    font-size: 1.5em;
    padding-top: 6px;
    padding-left: 15px;
}
.main {
    width: 1000px;
    margin: auto;
    border: 3px solid #ccc;
    border-radius: 10px;
    box-shadow: 5px 10px 10px #888;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

